<!--

Modal confirm

-->
<template>
  <div class="FullContainer">
    <div class="ConfirmActionMessage">{{getTitle()}}</div>
    <div class="ConfirmActionAcctions">
      <button
        class="ConfirmButton ConfirmActionAcctionsFirst"
        v-on:click="actionYesWithHandlerViewError"
      >
        <i class="far fa-thumbs-up"></i>
      </button>
      <button class="ConfirmButton" v-on:click="actionNoWithHandlerViewError">
        <i class="far fa-thumbs-down"></i>
      </button>
    </div>
  </div>
</template>

<script>
import BaseComponent from "../base/BaseComponent";
import CommonsUtils from "../../utils/CommonsUtils";
import ConstantsModals from "../../constants/ConstantsModals";

export default {
  extends: BaseComponent,
  name: "ModalComponent",
  props: {
    text: {
      type: String,
      default: ""
    }
  },
  methods: {
    /**
     *  Method execute yes action with handler view error
     */
    actionYesWithHandlerViewError: async function() {
      const self = this;
      self.getVueContext().applyAsyncFunctionWithHandlerViewError(() => {
        return self.actionYes();
      });
    },
    /**
     * Method execute yes action
     */
    actionYes: async function() {
      let closeModal = true;
      try {
        if (
          CommonsUtils.isNotNull(
            this.params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_ACTION_YES]
          )
        ) {
          closeModal = await this.params[
            ConstantsModals.KEY_MODAL_COMPONENT_PARAM_ACTION_COMPONENT
          ][
            this.params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_ACTION_YES]
          ]();
        }
        if (closeModal) {
          this.closeModal();
        }
      } catch (ex) {
        console.error(ex);
      }
    },
    /**
     *  Method execute no action with handler view error
     */
    actionNoWithHandlerViewError: async function() {
      const self = this;
      self.getVueContext().applyAsyncFunctionWithHandlerViewError(() => {
        return self.actionNo();
      });
    },
    /**
     * Method execute no action
     */
    actionNo: async function() {
      let closeModal = true;
      try {
        if (
          CommonsUtils.isNotNull(
            this.params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_ACTION_NO]
          )
        ) {
          closeModal = await this.params[
            ConstantsModals.KEY_MODAL_COMPONENT_PARAM_ACTION_COMPONENT
          ][this.params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_ACTION_NO]]();
        }
        if (closeModal) {
          this.closeModal();
        }
      } catch (ex) {
        console.log(ex);
      }
    },

    /**
     * Method to get title for modal confirm
     * @returns title for modal confirm
     */
    getTitle: function() {
      let title = "";
      if (
        CommonsUtils.isNotNull(
          this.params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_TEXT]
        )
      ) {
        title = this.params[ConstantsModals.KEY_MODAL_COMPONENT_PARAM_TEXT];
      }
      return title;
    }
  }
};
</script>

<style>
.ConfirmModal {
  width: 300px !important;
  max-height: 300px !important;
  top: calc(50% - 250px);
  left: calc(50% - 250px);
}

.ConfirmActionMessage {
  width: 100%;
  float: left;
  clear: left;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
  margin-top: 10px;
  margin-bottom: 10px;
  color: var(--rf-fontColor, #466289);
}

.ConfirmActionAcctions {
  margin-top: 10px;
  width: 100%;
  float: left;
  clear: left;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border-top: 1px solid var(--rf-fontColor, #466289);
}

.ConfirmActionAcctionsFirst {
  margin-left: 38%;
}

.ConfirmButton {
  width: 30px;
  height: 30px;
  color: var(--rf-buttonColor, #466289);
  background-color: transparent;
  border: none;
  float: left;
  cursor: pointer;
  outline: none;
}
</style>